@charset "UTF-8";
:root {
  color-scheme: dark light;
  --canvas: light-dark(var(--color-white), var(--bg-page));
  --ink: light-dark(var(--bg-page), var(--color-white));
}

:root {
  --spacing-block: 1rem;
  --spacing-inline: 1rem;
}

@font-face {
  font-family: "Clash Display";
  src: url("../fonts/ClashDisplay/ClashDisplay-Variable.woff2") format("woff2"), url("../fonts/ClashDisplay/ClashDisplay-Variable.woff") format("woff");
  font-weight: 200 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Satoshi";
  src: url("../fonts/Satoshi/Satoshi-Variable.woff2") format("woff2"), url("../fonts/Satoshi/Satoshi-Variable.woff") format("woff");
  font-weight: 300 900;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Satoshi";
  src: url("../fonts/Satoshi/Satoshi-VariableItalic.woff2") format("woff2"), url("../fonts/Satoshi/Satoshi-VariableItalic.woff") format("woff");
  font-weight: 300 900;
  font-style: italic;
  font-display: swap;
}
:root {
  --font-family-heading: 'Clash Display', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  --font-family-body: 'Satoshi', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  --font-family-mono: 'Fira Code', 'Cascadia Code', 'Consolas', 'Monaco', 'Courier New', monospace;
}

:root {
  --font-size-xs: 0.75rem;
  --font-size-sm: 0.875rem;
  --font-size-base: 1rem;
  --font-size-lg: 1.125rem;
  --font-size-xl: 1.25rem;
  --font-size-2xl: 1.5rem;
  --font-size-3xl: 2.5rem;
  --font-size-4xl: 3rem;
  --font-size-5xl: 3.625rem;
  --font-size-6xl: 4rem;
}

:root {
  --font-weight-light: 300;
  --font-weight-regular: 400;
  --font-weight-medium: 500;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;
  --font-weight-black: 900;
}

:root {
  --line-height-tight: 1.25;
  --line-height-normal: 1.5;
  --line-height-relaxed: 1.75;
  --line-height-loose: 2;
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--font-family-heading);
  font-weight: var(--font-weight-bold);
  line-height: var(--line-height-tight);
  margin-top: 0;
  margin-bottom: 0.5em;
  color: var(--base-color);
}

h1 {
  font-size: var(--font-size-5xl);
  font-weight: var(--font-weight-bold);
}

h2 {
  font-size: var(--font-size-4xl);
  font-weight: var(--font-weight-semibold);
}

h3 {
  font-size: var(--font-size-3xl);
  font-weight: var(--font-weight-semibold);
}

h4 {
  font-size: var(--font-size-2xl);
  font-weight: var(--font-weight-medium);
}

h5 {
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-medium);
}

h6 {
  font-size: var(--font-size-lg);
  font-weight: var(--font-weight-medium);
}

body {
  font-family: var(--font-family-body);
  font-size: var(--font-size-base);
  font-weight: var(--font-weight-regular);
  line-height: var(--line-height-normal);
}

p {
  margin-top: 0;
  margin-bottom: 1em;
}

strong, b {
  font-weight: var(--font-weight-bold);
}

em, i {
  font-style: italic;
}

footer {
  grid-row: footer;
}

body > .layout {
  align-items: center;
  display: grid;
  column-gap: var(--spacing-inline);
  row-gap: var(--spacing-block);
  grid-auto-columns: 1fr;
  grid-template-columns: [full-width] auto;
  grid-template-rows: [header-start] auto [header-end main-start] 1fr [main-end footer-start] auto [footer-end];
  justify-items: stretch;
  max-width: 100svw;
  min-height: 100vh;
}

.content-grid {
  --padding-inline: 1rem;
  --content-max-width: 900px;
  --breakout-max-width: 1200px;
  --breakout-size: calc(
    (var(--breakout-max-width) - var(--content-max-width)) / 2
  );
  display: grid;
  grid-template-columns: [full-width-start] minmax(var(--padding-inline), 1fr) [breakout-start] minmax(0, var(--breakout-size)) [content-start] min(100% - var(--padding-inline) * 2, var(--content-max-width)) [content-end] minmax(0, var(--breakout-size)) [breakout-end] minmax(var(--padding-inline), 1fr) [full-width-end];
}

.content-grid > :not(.breakout, .full-width),
.full-width > :not(.breakout, .full-width) {
  grid-column: content;
}

.content-grid > .breakout {
  grid-column: breakout;
}

.content-grid > .full-width {
  display: grid;
  grid-column: full-width;
  grid-template-columns: inherit;
}

img.full-width {
  width: 100%;
  max-height: 45vh;
  object-fit: cover;
}

header[role=banner] {
  border-block-end: 1px solid rgba(from var(--color-white) r g b/0.2);
  grid-column: full-width;
  grid-row: header;
  padding-block: var(--spacing-block);
  z-index: 1000;
}
header[role=banner] > div {
  align-items: center;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
}
header[role=banner] nav {
  margin-inline: auto;
}

main {
  grid-row: main;
}

.navigation {
  align-items: center;
  background: conic-gradient(from 180deg at 50% 50%, var(--gradient-start), var(--gradient-end));
  border-radius: 20px;
  corner-shape: round;
  display: flex;
  font-family: var(--font-family-body);
  gap: 2rem;
  justify-content: space-between;
  padding: 12px 24px;
}

.nav-menu {
  align-items: center;
  display: flex;
  gap: 1.5rem;
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav-menu__link {
  color: var(--grey-600);
  font-family: var(--font-family-body);
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-medium);
  letter-spacing: 0.01em;
  text-decoration: none;
  padding-block: 0.5rem;
  padding-inline: 0.75rem;
  position: relative;
  transition: color 0.2s ease;
}
.nav-menu__link:hover {
  color: var(--color-white);
}
.nav-menu__link:focus {
  outline: none;
}
.nav-menu__link:focus-visible {
  border-radius: 4px;
  color: var(--color-white);
  outline: 2px solid var(--glow-mid);
  outline-offset: 4px;
}
.nav-menu__link.is-active, .nav-menu__link[aria-current=page] {
  color: var(--color-white);
}
.nav-menu__link.is-active::after, .nav-menu__link[aria-current=page]::after {
  background: linear-gradient(90deg, var(--gradient-start), var(--gradient-end));
  border-radius: 1px;
  bottom: 0;
  content: "";
  height: 2px;
  left: 0.75rem;
  position: absolute;
  right: 0.75rem;
}

.nav-toggle {
  display: none;
}

section {
  display: grid;
}

i.galaxy-icon {
  aspect-ratio: 1/1;
  background: var(--color-white);
  border-radius: 50%;
  display: block;
  content: "";
  corner-shape: scoop;
  position: relative;
  width: 1lh;
}
i.galaxy-icon::after, i.galaxy-icon::before {
  aspect-ratio: inherit;
  background: inherit;
  border-radius: inherit;
  content: "";
  corner-shape: inherit;
  position: absolute;
}
i.galaxy-icon::after {
  right: 0;
  top: 0;
  width: 0.3lh;
}
i.galaxy-icon::before {
  bottom: -5px;
  right: -4px;
  width: 0.5lh;
}

.header-cta {
  align-items: center;
  display: flex;
}

.header-cta__button {
  align-items: center;
  display: inline-flex;
  gap: 0.5rem;
  justify-content: center;
  min-width: 100px;
  padding-block: 0.625rem;
  padding-inline: 1.25rem;
  font-family: var(--font-family-body);
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-semibold);
  text-decoration: none;
  background: linear-gradient(135deg, var(--gradient-start), var(--gradient-end));
  color: var(--color-white);
  border: none;
  border-radius: 8px;
  cursor: pointer;
  box-shadow: 0 4px 16px -4px color-mix(in oklch, var(--gradient-end) 40%, transparent), inset 0 1px 0 color-mix(in oklch, var(--color-white) 15%, transparent);
  transition: box-shadow 0.2s ease, filter 0.2s ease, transform 0.2s ease;
}
.header-cta__button:hover {
  box-shadow: 0 8px 24px -4px color-mix(in oklch, var(--gradient-end) 50%, transparent), inset 0 1px 0 color-mix(in oklch, var(--color-white) 20%, transparent);
  filter: brightness(1.1);
  transform: translateY(-2px);
}
.header-cta__button:active {
  box-shadow: 0 2px 8px -2px color-mix(in oklch, var(--gradient-end) 30%, transparent), inset 0 1px 0 color-mix(in oklch, var(--color-white) 10%, transparent);
  filter: brightness(0.95);
  transform: translateY(0);
}
.header-cta__button:focus {
  outline: none;
}
.header-cta__button:focus-visible {
  outline: 2px solid var(--glow-core);
  outline-offset: 3px;
}

.header-cta__button--outline {
  background: transparent;
  border: 1px solid var(--base-600);
  box-shadow: none;
  color: var(--color-white);
}
.header-cta__button--outline:hover {
  background: color-mix(in oklch, var(--base-600) 20%, transparent);
  border-color: var(--base-700);
  filter: none;
  transform: none;
}

.hero {
  display: grid;
  height: 33.75vw; /* Half the circle height (67.5vw รท 2) */
  isolation: isolate;
  place-items: center;
  position: relative;
}

/* Content Layer - Top */
.hero .container {
  position: relative;
  z-index: 4;
}

/* Circle Layer - With corona glow */
.hero::before {
  aspect-ratio: 16/9;
  background: var(--canvas);
  border-radius: 50%;
  box-shadow: 0 0 20px 8px var(--base-700), 0 0 40px 15px var(--base-800), 0 0 80px 30px rgba(189, 173, 224, 0.3);
  content: "";
  display: block;
  left: 50%;
  max-width: 120vw;
  position: absolute;
  top: 0;
  transform: translate(-50%, -50%);
  width: 120vw;
  z-index: 3;
}

/* Purple Side Glows Layer */
.hero-glow {
  height: 100%;
  left: 0;
  pointer-events: none;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1;
}

.hero-glow::before {
  aspect-ratio: 1/1.2;
  background: radial-gradient(ellipse 60% 80% at 40% 50%, var(--glow-mid) 0%, var(--base-700) 20%, var(--base-600) 40%, transparent 70%), radial-gradient(ellipse 80% 100% at 60% 50%, var(--base-600) 0%, var(--base-500) 30%, transparent 60%);
  content: "";
  display: block;
  filter: blur(100px);
  height: 500px;
  opacity: 0.8;
  position: absolute;
  right: 0%;
  top: 30%;
  width: 400px;
}

.hero-glow::after {
  aspect-ratio: 1.5/1;
  background: radial-gradient(ellipse 70% 60% at 50% 40%, var(--glow-soft) 0%, var(--base-800) 20%, var(--base-700) 40%, transparent 70%), radial-gradient(ellipse 90% 80% at 50% 50%, var(--base-700) 0%, var(--base-600) 30%, transparent 60%);
  bottom: -20%;
  content: "";
  display: block;
  filter: blur(120px);
  height: 350px;
  left: 50%;
  opacity: 0.7;
  position: absolute;
  transform: translateX(-50%);
  width: 500px;
}

/* White Bottom Glow Layer */
.hero::after {
  aspect-ratio: 4/1;
  background: radial-gradient(ellipse 30% 50% at 50% 50%, var(--color-white) 0%, var(--color-white) 20%, var(--glow-core) 40%, transparent 100%), radial-gradient(ellipse 50% 70% at 50% 50%, var(--glow-core) 0%, var(--base-900) 30%, var(--base-800) 50%, transparent 100%), radial-gradient(ellipse 70% 90% at 50% 50%, var(--base-800) 0%, var(--base-700) 40%, var(--base-600) 60%, transparent 100%);
  bottom: -8%;
  content: "";
  display: block;
  filter: blur(25px);
  height: 80px;
  left: 50%;
  opacity: 1; /* Increased from 0.9 */
  position: absolute;
  transform: translateX(-50%);
  width: 600px;
  z-index: 0;
}

.hero-starburst {
  background: repeating-conic-gradient(from 0deg at 80% 20%, transparent 0deg, var(--base-700) 2deg, var(--base-800) 4deg, transparent 6deg, transparent 30deg);
  filter: blur(30px);
  height: 1000px;
  opacity: 0.5;
  pointer-events: none;
  position: absolute;
  right: -40%;
  top: -60%;
  width: 1000px;
  z-index: 4;
}

.logo {
  align-items: center;
  color: var(--color-white);
  display: flex;
  flex-flow: row nowrap;
  gap: 0.25lh;
  justify-content: flex-start;
  font-family: var(--font-family-heading);
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-regular);
  text-decoration: none;
  text-shadow: 0 0 20px color-mix(in oklch, var(--glow-mid) 30%, transparent);
}

.news-bar {
  background: transparent;
  border: none;
  inset: unset;
  margin: 0;
  padding: 0;
  height: var(--news-bar-height);
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 101;
  background: linear-gradient(90deg, color-mix(in oklch, var(--base-400) 80%, transparent), color-mix(in oklch, var(--base-500) 80%, transparent), color-mix(in oklch, var(--base-400) 80%, transparent));
  backdrop-filter: blur(8px);
  border-bottom: 1px solid color-mix(in oklch, var(--base-600) 40%, transparent);
  opacity: 1;
  transform: translateY(0);
  transition: opacity 0.3s ease, transform 0.3s ease;
}
.news-bar:not(:popover-open) {
  opacity: 0;
  pointer-events: none;
  transform: translateY(-100%);
}

.news-bar__content {
  align-items: center;
  display: flex;
  gap: 1rem;
  height: 100%;
  justify-content: center;
  margin-inline: auto;
  max-width: var(--content-max-width, 1200px);
  padding-inline: 1.5rem;
}

.news-bar__message {
  align-items: center;
  color: var(--grey-700);
  display: flex;
  font-family: var(--font-family-body);
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-regular);
  gap: 0.75rem;
  margin: 0;
}

.news-bar__badge {
  align-items: center;
  background: linear-gradient(135deg, var(--gradient-start), var(--gradient-end));
  border-radius: 4px;
  color: var(--color-white);
  display: inline-flex;
  font-size: var(--font-size-xs);
  font-weight: var(--font-weight-semibold);
  letter-spacing: 0.05em;
  padding-block: 2px;
  padding-inline: 0.5rem;
  text-transform: uppercase;
}

.news-bar__link {
  color: var(--color-white);
  font-weight: var(--font-weight-medium);
  text-decoration: underline;
  text-underline-offset: 3px;
  transition: color 0.2s ease;
}
.news-bar__link:hover {
  color: var(--glow-core);
}
.news-bar__link:focus-visible {
  border-radius: 2px;
  outline: 2px solid var(--glow-mid);
  outline-offset: 2px;
}

.news-bar__close {
  appearance: none;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  align-items: center;
  display: flex;
  justify-content: center;
  height: 32px;
  width: 32px;
  border-radius: 4px;
  color: var(--grey-600);
  transition: background-color 0.2s ease, color 0.2s ease;
}
.news-bar__close:hover {
  background: color-mix(in oklch, var(--color-white) 10%, transparent);
  color: var(--color-white);
}
.news-bar__close:focus {
  outline: none;
}
.news-bar__close:focus-visible {
  outline: 2px solid var(--glow-mid);
  outline-offset: 2px;
}
.news-bar__close svg {
  height: 16px;
  width: 16px;
}

body:has(.news-bar:popover-open) {
  padding-top: calc(var(--news-bar-height) + var(--header-height));
}

body:has(.news-bar:popover-open) .header {
  top: var(--news-bar-height);
}

.stars-wrapper {
  aspect-ratio: 16/9;
  background: var(--canvas);
  border-radius: 50%;
  height: auto;
  left: 50%;
  max-width: 120vw;
  mask-image: linear-gradient(black, transparent);
  overflow: hidden;
  perspective: 340px;
  position: absolute;
  top: 0;
  transform: translate(-50%, -50%);
  width: 120vw;
  z-index: 3; /* Same level as circle */
}

